'use client'

import { useState } from 'react'
import Link from 'next/link'
import { useRouter } from 'next/navigation'
import {
  AppBar,
  Toolbar,
  Container,
  Button,
  IconButton,
  Menu,
  MenuItem,
  Box,
  Typography,
  useMediaQuery
} from '@mui/material'
import MenuIcon from '@mui/icons-material/Menu'
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth'
import LoginIcon from '@mui/icons-material/Login'
import PersonAddIcon from '@mui/icons-material/PersonAdd'
import DashboardIcon from '@mui/icons-material/Dashboard'
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings'
import LogoutIcon from '@mui/icons-material/Logout'
import { useAuth } from '@/context/AuthContext'

export default function Navbar() {
  const router = useRouter()
  const { user, logout } = useAuth()
  const role = user?.role
  // SSR-sicheres Breakpoint-Query (MUI rendert clientseitig; noSsr verhindert Hydration-Issues)
  const isMobile = useMediaQuery('(max-width:899px)', { noSsr: true })

  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null)
  const open = Boolean(anchorEl)
  const handleMenu = (e: React.MouseEvent<HTMLButtonElement>) => setAnchorEl(e.currentTarget)
  const handleClose = () => setAnchorEl(null)

  const handleLogout = () => {
    logout()
    router.push('/login')
  }

  const Brand = (
    <Typography
      variant="h6"
      sx={{ fontWeight: 800, cursor: 'pointer', letterSpacing: '-0.02em' }}
      onClick={() => router.push('/')}
    >
      MeinFrisör
    </Typography>
  )

  const AuthButtons = (
    <>
      {!user ? (
        <>
          <Button
            startIcon={<LoginIcon />}
            color="inherit"
            onClick={() => router.push('/login')}
          >
            Login
          </Button>
          <Button
            startIcon={<PersonAddIcon />}
            variant="contained"
            onClick={() => router.push('/register')}
          >
            Registrieren
          </Button>
        </>
      ) : (
        <>
          <Button
            startIcon={<CalendarMonthIcon />}
            color="inherit"
            onClick={() => router.push('/booking')}
          >
            Buchen
          </Button>
          <Button
            startIcon={<DashboardIcon />}
            color="inherit"
            onClick={() => router.push('/dashboard')}
          >
            Dashboard
          </Button>
          {role === 'admin' && (
            <Button
              startIcon={<AdminPanelSettingsIcon />}
              color="inherit"
              onClick={() => router.push('/admin')}
            >
              Admin
            </Button>
          )}
          {role === 'staff' && (
            <Button
              startIcon={<CalendarMonthIcon />}
              color="inherit"
              onClick={() => router.push('/staff-dashboard')}
            >
              Staff
            </Button>
          )}
          <Button
            startIcon={<LogoutIcon />}
            variant="outlined"
            onClick={handleLogout}
          >
            Logout
          </Button>
        </>
      )}
    </>
  )

  return (
    <AppBar
      position="sticky"
      color="transparent"
      elevation={1}
      sx={{
        backdropFilter: 'blur(6px)',
        backgroundColor: 'rgba(255,255,255,0.85)',
      }}
    >
      <Container maxWidth="lg">
        <Toolbar disableGutters sx={{ gap: 1 }}>
          {/* Brand */}
          {Brand}

          {/* Desktop */}
          {!isMobile && (
            <Box sx={{ ml: 'auto', display: 'flex', gap: 1, alignItems: 'center' }}>
              <Button color="inherit" onClick={() => router.push('/booking')}>
                Termin buchen
              </Button>
              {AuthButtons}
            </Box>
          )}

          {/* Mobile */}
          {isMobile && (
            <Box sx={{ ml: 'auto' }}>
              <IconButton
                size="large"
                color="inherit"
                onClick={handleMenu}
                aria-controls={open ? 'nav-menu' : undefined}
                aria-haspopup="true"
                aria-expanded={open ? 'true' : undefined}
              >
                <MenuIcon />
              </IconButton>
              <Menu
                id="nav-menu"
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
                anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                transformOrigin={{ vertical: 'top', horizontal: 'right' }}
              >
                <MenuItem onClick={() => { handleClose(); router.push('/booking') }}>
                  Termin buchen
                </MenuItem>

                {!user ? (
                  <>
                    <MenuItem onClick={() => { handleClose(); router.push('/login') }}>
                      Login
                    </MenuItem>
                    <MenuItem onClick={() => { handleClose(); router.push('/register') }}>
                      Registrieren
                    </MenuItem>
                  </>
                ) : (
                  <>
                    <MenuItem onClick={() => { handleClose(); router.push('/dashboard') }}>
                      Dashboard
                    </MenuItem>
                    {role === 'admin' && (
                      <MenuItem onClick={() => { handleClose(); router.push('/admin') }}>
                        Admin
                      </MenuItem>
                    )}
                    {role === 'staff' && (
                      <MenuItem onClick={() => { handleClose(); router.push('/staff-dashboard') }}>
                        Staff
                      </MenuItem>
                    )}
                    <MenuItem onClick={() => { handleClose(); handleLogout() }}>
                      Logout
                    </MenuItem>
                  </>
                )}
              </Menu>
            </Box>
          )}
        </Toolbar>
      </Container>
    </AppBar>
  )
}
